import "./outlinePlot.scss";
import React from "react";

function createLine(data, dataKey, canvasWidth, canvasHeight) {
    let lineString = "M 0,0 ";
    let minValue = Number.MAX_VALUE;
    let maxValue = -Number.MAX_VALUE;
    for (let i = 0; i < data.length; i++) {
        minValue = Math.min(minValue, data[i][dataKey]);
        maxValue = Math.max(maxValue, data[i][dataKey]);
    }

    const minPlotWidth = 2;
    minValue -= Math.max(minPlotWidth, (maxValue - minValue) * 0.2);

    const valueRange = Math.max(10, maxValue - minValue);
    const xStep = canvasWidth / valueRange;
    const yStep = canvasHeight / 24;

    for (let i = 0; i < data.length; i++) {
        const currentValue = data[i][dataKey] - minValue;
        lineString += "H " + (currentValue * xStep).toString() + " ";
        lineString += "v " + yStep.toString() + " ";
    }

    lineString += "L 0," + (data.length * yStep).toString() + " z";

    return lineString;
}

function draw(context, data, dataKey) {

    const canvas = context.canvas;
    context.save();
    context.clearRect(0, 0, canvas.width, canvas.height);

    let path = new Path2D(createLine(data, dataKey, canvas.width, canvas.height));

    //context.fillStyle = 'rgba(255, 0, 0, 1)';
    //context.fill(path);
    context.restore();
    context.save();

    /*context.shadowColor = "black";
    context.shadowBlur = 10;
    context.shadowOffsetX = 0;
    context.shadowOffsetY = 0;*/


    // invert alpha channel
    /*context.globalCompositeOperation = "xor";
    context.fillRect(0, 0, canvas.width, canvas.height);

    // draw itself again using drop-shadow filter
    context.shadowBlur = 7*2;  // use double of what is in CSS filter (Chrome x4)
    context.shadowOffsetX = context.shadowOffsetY = 5;
    context.shadowColor = "#000";
    context.stroke(path);

    // draw original image with background mixed on top
    context.globalCompositeOperation = "destination-atop";
    context.shadowColor = "transparent";                  // remove shadow !
    context.stroke(path);
  */
    context.restore();
    context.save();
    context.clip(path);
    context.lineWidth = 5;
    context.strokeStyle = 'rgba(255, 255, 255, 1)';
    context.stroke(path);
    context.restore();

    //requestAnimationFrame(() => draw(context, data));
}

export default function OutlinePlot({ data, dataKey, dayID }) {
    const dayInWord = dayID === 0 ? "Today" : "Tomorrow";
    const plotID = dataKey.toString() + dayInWord;

    const canvasRef = React.useRef();

    React.useEffect(() => {
        const context = canvasRef.current.getContext("2d");
        requestAnimationFrame(() => draw(context, data, dataKey));

        const handleResize = (e) => {
            const canvasContainer = document.getElementById(plotID);
            context.canvas.height = canvasContainer.offsetHeight;
            context.canvas.width = canvasContainer.offsetWidth;
            draw(context, data, dataKey);
        };

        handleResize();
        window.addEventListener("resize", handleResize);

        return () => window.removeEventListener("resize", handleResize);
    }, []);

    React.useEffect(() => {
        const context = canvasRef.current.getContext("2d");
        draw(context, data, dataKey);
    }, [data]);

    return (
        <div className="canvasContainer" id={plotID}>
            <canvas id="canvas" ref={canvasRef} />
        </div>
    );
};