import "./plotForADay.scss";
import React from 'react';
import OutlinePlot from "../outlinePlot/OutlinePlot";
import DataListItem from "../dataListItem/DataListItem";
import {ReactComponent as SunnyIcon} from "../../assets/sunny_icon.svg";
import {ReactComponent as CloudyIcon} from "../../assets/cloudy_icon.svg";
import {ReactComponent as WindDirectionIcon} from "../../assets/wind_direction_icon.svg";

export default function PlotForADay({dayID,
                                        plotID,
                                        currentHour,
                                        weatherData,
                                        dataKey,
                                        plotKey,
                                        unit,
                                        sunrise,
                                        sunset,
                                        decimalPrecision = 0}) {
    return (
        <div className="plotAndDataContainer">
            <div className="iconContainer">
                <ul>
                    {[...Array(24)].map((x, hourOfDay) =>
                        (unit === "km/h") ? (
                                <WindDirectionIcon className="sunnyIcon" style={{transform: `rotateZ(${hourOfDay * 13}deg)`}}/>
                            ) : (
                                hourOfDay < 12 ? (
                                    <CloudyIcon className="sunnyIcon"/>

                                ) : (
                                    <SunnyIcon className="sunnyIcon"/>

                                )
                        )
                    )}
                </ul>
            </div>
            <div className="dataContainer">
                <ul>
                    {[...Array(24)].map((x, hourOfDay) =>
                        <DataListItem
                            key={hourOfDay}
                            data={weatherData[hourOfDay][dataKey]}
                            time={hourOfDay}
                            active={currentHour === hourOfDay}
                            sunrise={sunrise}
                            sunset={sunset}
                        />
                    )}
                </ul>
            </div>
            <div className="plotContainer">
                <OutlinePlot
                    data={weatherData}
                    dataKey={plotKey}
                    dayID={dayID}
                />
            </div>
        </div>
    );
}
